/* ==================== ==================== ====================
 * 3-sections - Contact
 * ==================== ==================== ==================== */
.contact {

  &__form {
    background: {
      color: #111;
      image: url("../img/bgs/contact-bg/contact-bg_480x895.jpg");
      size: cover;
      position: 82% center;
      repeat: no-repeat; }

    /* Media Queries for responsive background image */
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
      background: {
        image: url("../img/bgs/contact-bg/contact-bg_480x895@2x.jpg"); } }

    // 481px
    @media (min-width: 30.0625em) {
      background: {
        image: url("../img/bgs/contact-bg/contact-bg_812x895.jpg"); } }

    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) and (min-width: 30.0625em) {
      background: {
        image: url("../img/bgs/contact-bg/contact-bg_812x895@2x.jpg"); } }

    // 813px
    @media (min-width: 50.8125em) {
      background: {
        image: url("../img/bgs/contact-bg/contact-bg_1200x855.jpg"); } }

    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) and (min-width: 50.8125em) {
      background: {
        image: url("../img/bgs/contact-bg/contact-bg_1200x855@2x.jpg"); } }

    // 1201px
    @media (min-width: 75.0625em) {
      background: {
        image: url("../img/bgs/contact-bg/contact-bg_1451x855.jpg"); } }

    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) and (min-width: 75.0625em) {
      background: {
        image: url("../img/bgs/contact-bg/contact-bg_1451x855@2x.jpg"); } } }

  // ==================== Contact / Title ====================
  .common__title {
    color: $socondary-text-color;
    padding-top: 16rem;

    span::before {
      content: '';
      margin-left: .3em; } } }

/* ==================== ==================== ====================
 * 3-sections - Contact / Form
 * ==================== ==================== ==================== */
.form {
  margin-left: 1rem;
  margin-right: 1rem;

  // 1281px
  @media (min-width: 80.0625em) {
    margin-left: auto;
    margin-right: auto; }

  &__wrapper {
    display: flex;
    flex-direction: column;

    // 813px
    @media (min-width: 50.8125em) {
      flex-direction: row; } }

  &__input-wrap {
    margin: .5rem 0;

    // 813px
    @media (min-width: 50.8125em) {
      flex: 1;

      &:nth-child(2) {
        margin-left: 2rem;
        margin-right: 2rem; } } }

  &__input,
  &__txt-area {
    background-color: transparent;
    color: $socondary-text-color;
    border: .1rem solid #eee;
    text-align: center;
    text-transform: capitalize;
    display: block;
    width: 100%;
    font-family: 'PT Sans', sans-serif;
    padding: 1.6rem; }

  &__input::placeholder,
  &__txt-area::placeholder {
    color: darken($socondary-text-color, 25%); }

  &__input:focus::placeholder,
  &__txt-area:focus::placeholder {
    opacity: .5; }

  &__txt-area {
    height: 25rem;
    text-align: left;
    margin-bottom: 4rem;

    &::placeholder {
      text-align: center; } }

  // ==================== Form / Button ====================
  &__btn {
    width: 100%; } }

/* ==================== ==================== ====================
 * 3-sections - Contact / Address
 * ==================== ==================== ==================== */
.address {

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 4rem; }

  &__item {
    display: flex;
    align-items: center;

    &:nth-child(2) {
      margin: 3rem 0; } }

  &__icon {
    // color: red
    // fill: currentColor
    min-width: 6rem;
    min-height: 4rem;
    margin-left: 1rem;
    margin-right: 3rem; }

  &__text {
    font-style: normal;
    text-transform: uppercase;
    text-align: left;

    // Changes color of the link/phone-number to the base text color.
    // I don't know why but it's only visible on an actual device,
    // that difference is invisible on Chrome's Dev Tools
    a {
      color: $text-color; }

    // Used because the email address is to long, what causes a problem on mobile widhts.
    &--small {
      font-size: 1.7rem;

      // 384px
      @media (min-width: 24em) {
        font-size: 2rem; }

      // 448px
      @media (min-width: 28em) {
        font-size: 2.4rem; } }

    // Causes line brake at the email address, without useing a <br> tag.
    // 592px
    @media (max-width: 37em) {
      span::after {
        content: '\A';
        white-space: pre; } } } }
